body {
    margin: 0px;
    padding: 0px;
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

* {
    font-family: 'Roboto', sans-serif;
    font-size: 12px;
}

h1, h2, h3, h4, h5, h6 {
    margin: 0px;
    padding: 0px;
}
